.chat {
  width: 100%;
  height: 120vh;
  background: linear-gradient(
    113.46deg,
    #000000 10.07%,
    #031c33 51.72%,
    #004c92 102%
  );
}

.header {
  padding-top: 60px;
}

h2 {
  font-family: Poppins;
  margin: 20px auto;
  text-align: center;
  color: #c5d4d7;
  font-size: 40px;
  max-width: 600px;
  position: relative;
}
h2:before {
  content: "";
  display: block;
  width: 600px;
  height: 3px;
  background: #39becd;
  right: 600px;
  top: 45%;
  position: absolute;
}
h2:after {
  content: "";
  display: block;
  width: 600px;
  height: 3px;
  background: #39becd;
  left: 600px;
  top: 45%;
  position: absolute;
}

.need_svg {
  width: 530px;
  margin-left: 350px;
}
.customer_svg {
  width: 560px;
  margin-left: 120px;
}
.svg_area {
  display: flex;
  margin-top: 100px;
}
